<template>
    <el-dialog title="内容类型详情" align-center width="640px">
        <div class="info-parent">
            <MyIcon class="info-icon" :src="item.icon" :title="item.type" :size="99" />

            <el-form class="info-form" label-width="120px">
                <el-form-item label="类型"> {{ item.type }} </el-form-item>

                <el-form-item label="ID / UUID"> {{ item.id }} / {{ item.uuid }} </el-form-item>
                <el-form-item label="URN"> {{ item.urn }} </el-form-item>
                <el-form-item label="显示名称"> {{ item.label }} </el-form-item>
                <el-form-item label="命名模式"> {{ item.patterns }} </el-form-item>
                <el-form-item label="文件/目录/工程"> {{ item.as_file }} / {{ item.as_dir }} / {{ item.as_project }}
                </el-form-item>
                <el-form-item label="优先级"> {{ item.priority }} </el-form-item>

                <el-form-item label="描述"> {{ item.description }} </el-form-item>

                <el-form-item label="创建时间"> {{ formatTime(item.created_at) }} </el-form-item>
                <el-form-item label="修改时间"> {{ formatTime(item.updated_at) }} </el-form-item>
            </el-form>
        </div>

        <div>
            <ElButton type="default" @click="close">关闭</ElButton>
        </div>
    </el-dialog>
</template>

<script>
import { ElButton } from 'element-plus';
import MyIcon from '@/components/utils/icon.vue'
import TimeJS from '@/js/time.js'

export default {
    name: "settings-local-repository-item-detail-dialog",

    components: { ElButton, MyIcon },

    computed: {
        item() {
            return this.target;
        }
    },

    methods: {
        close() {
            this.$emit('update:modelValue', false)
        },

        formatTime(ms) {
            return TimeJS.StringifyTimestamp(ms, 'datetime')
        },
    },

    props: {
        target: Object
    }
}
</script>

<style>
.info-parent {
    display: flex;
    flex-direction: row;
}

.info-icon {
    flex-grow: 0;
}

.info-form {
    flex-grow: 1;
}
</style>
