<template>
  <div>
    <el-table :data="items" @selection-change="handleSelectionChange">
      <el-table-column type="selection"></el-table-column>

      <el-table-column prop="type" label="" width="48px">
        <template #default="scope">
          <ImageView
            v-model="scope.row.icon"
            image-height="24px"
            image-width="24px"
          />
        </template>
      </el-table-column>

      <el-table-column prop="type" label="类型" sortable></el-table-column>

      <el-table-column
        prop="patterns"
        label="命名模式"
        sortable
      ></el-table-column>

      <el-table-column prop="label" label="标签" sortable></el-table-column>

      <el-table-column
        prop="priority"
        label="优先级"
        sortable
      ></el-table-column>

      <el-table-column label="操作" width="170px">
        <template #default="scope">
          <el-button
            link
            type="primary"
            @click="handleClickItemDetail(scope.row)"
            >详细</el-button
          >

          <el-button link type="primary" @click="handleClickEditItem(scope.row)"
            >编辑</el-button
          >

          <el-button
            link
            type="danger"
            @click="handleClickRemoveItem(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <MyBatch v-model="selectedItems" />

    <MyEditDialog
      v-model="displayEditDialog"
      title="编辑类型"
      method="PUT"
      :target="theEditDialogTarget"
    />

    <MyItemDetailDialog
      v-model="displayDetailDialog"
      :target="theDetailDialogItem"
    />
  </div>
</template>
    
<script>
import MyEditDialog from "./edit-dialog.vue";
import ImageView from "@/components/media/image-view.vue";
import { ElMessageBox } from "element-plus";
import MyBatch from "./batch.vue";
import MyItemDetailDialog from "./item-detail-dialog.vue";

export default {
  name: "settings-project-types-table",

  components: { MyEditDialog, ImageView, MyBatch, MyItemDetailDialog },

  computed: {
    items() {
      return this.$store.getters["content_type/all"];
    },
  },

  data() {
    return {
      displayEditDialog: false,
      theEditDialogTarget: {},
      theDetailDialogTarget: {},
      displayDetailDialog: false,
      selectedItems: [],
    };
  },

  methods: {
    handleClickEditItem(item) {
      this.theEditDialogTarget = item;
      this.displayEditDialog = true;
    },

    handleClickRemoveItem(item) {
      let title = "删除项目类型";
      let text = "确定要移除 [" + item.name + "] 这项目类型吗？";
      let desc = "";
      ElMessageBox.confirm(text + desc, title, {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeItem(item);
        })
        .catch(() => {});
    },

    handleClickItemDetail(item) {
      this.theDetailDialogItem = item;
      this.displayDetailDialog = true;
    },

    removeItem(item) {
      this.$store.dispatch("content_type/remove", item);
    },

    handleSelectionChange(items) {
      this.selectedItems = items;
    },
  },
};
</script>
