<template>
  <el-dialog>
    <el-form label-width="100px">
      <el-form-item label="图标">
        <ImageSelect
          v-model="item.icon"
          image-height="100px"
          image-width="100px"
        />
      </el-form-item>

      <el-form-item label="类型名称">
        <el-input v-model="item.type" placeholder="例如：text/plain"></el-input>
      </el-form-item>

      <el-form-item label="命名模式">
        <el-input
          v-model="item.patterns"
          placeholder="例如：*.txt; 可接受多个值，以逗号分隔"
        ></el-input>
      </el-form-item>

      <el-form-item label="目标">
        <el-checkbox v-model="item.as_dir"> 文件夹 </el-checkbox>
        <el-checkbox v-model="item.as_file"> 文件 </el-checkbox>
        <el-checkbox v-model="item.as_project"> 项目 </el-checkbox>
      </el-form-item>

      <el-form-item label="标签名">
        <el-input v-model="item.label"></el-input>
      </el-form-item>

      <el-form-item label="优先级">
        <!-- <el-input v-model="item.priority" type="number"></el-input> -->
        <MyPrioritySelector v-model="item.priority" />
      </el-form-item>

      <el-form-item label="描述">
        <el-input v-model="item.description" type="textarea"></el-input>
      </el-form-item>
    </el-form>

    <div>
      <el-button type="success" @click="save">保存</el-button>
      <el-button type="default" @click="close">取消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import MyPrioritySelector from "./priority-selector.vue";
import ImageSelect from "@/components/media/image-select.vue";

export default {
  name: "settings-project-type-edit-dialog",

  components: { MyPrioritySelector, ImageSelect },

  data() {
    return {
      item: {
        as_dir: false,
        as_file: false,
        description: "",
        label: "",
        name: "",
        type: "",
      },
    };
  },

  methods: {
    save() {
      let item = this.item;
      let action = "";
      let met = this.method;
      if (met == "POST") {
        action = "insert";
      } else if (met == "PUT") {
        action = "update";
      } else {
        return;
      }
      this.$store.dispatch("content_type/" + action, { item }).then(() => {
        this.close();
      });
    },

    close() {
      this.$emit("update:modelValue", false);
    },

    cloneObject(o) {
      let str = JSON.stringify(o);
      return JSON.parse(str);
    },
  },

  props: {
    method: String,
    target: Object,
  },

  watch: {
    target(value) {
      this.item = this.cloneObject(value);
    },
  },
};
</script>
