<template>
  <div class="toolbar">
    <div>
      <el-button title="刷新" @click="fetch">
        <el-icon> <Refresh /> </el-icon>
      </el-button>

      <el-button title="添加" @click="insert">
        <el-icon> <Plus /> </el-icon>
      </el-button>

      <el-dropdown style="margin-left: 15px" @command="handleCommand">
        <el-button type="default" title="更多">
          <el-icon><More /></el-icon>
        </el-button>
        <template #dropdown>
          <el-dropdown-menu>
            <el-dropdown-item command="IMPORT_PRESET_TYPES">
              导入预置类型
            </el-dropdown-item>

            <!-- <el-dropdown-item>Action 2</el-dropdown-item>
            <el-dropdown-item>Action 3</el-dropdown-item>
            <el-dropdown-item>Action 4</el-dropdown-item>
            <el-dropdown-item>Action 5</el-dropdown-item> -->
          </el-dropdown-menu>
        </template>
      </el-dropdown>
    </div>

    <MyEditDialog
      v-model="displayEditDialog"
      method="POST"
      :target="theEditDialogTarget"
      title="添加类型"
    />
  </div>
</template>

<script setup>
import { Refresh, Plus, More } from "@element-plus/icons-vue";
</script>

<script>
import MyEditDialog from "./edit-dialog.vue";

export default {
  name: "settings-project-types-toolbar",

  components: { MyEditDialog },

  data() {
    return {
      displayEditDialog: false,
      theEditDialogTarget: {},
    };
  },

  methods: {
    fetch() {
      this.$store.dispatch("content_type/fetch");
    },

    insert() {
      this.theEditDialogTarget = {};
      this.displayEditDialog = true;
    },

    importPresetTypes() {
      let method = "POST";
      let type = "import-project-types";
      let data = {};
      this.$store.dispatch("rest/execute", { method, type, data });
    },

    handleCommand(cmd) {
      if (cmd == "IMPORT_PRESET_TYPES") {
        this.importPresetTypes();
      }
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>

<style scoped>
.toolbar {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 20px;
  margin-bottom: 20px;
}
</style>
