<template>
  <!-- 批处理工具栏 -->
  <div style="margin-top: 10px">
    <BatchToolBar v-model="items" title="批处理" :hideEmpty="true">
      <el-button type="danger" @click="handleDelete"> 删除 </el-button>
      <el-button type="primary" @click="handlePutToBuffer">
        存入缓冲区
      </el-button>
    </BatchToolBar>
  </div>
</template>

<script>
import BatchToolBar from "@/components/utils/batch_tool_bar.vue";

export default {
  name: "settings-intent-templates-batch",

  components: { BatchToolBar },

  computed: {
    items() {
      return this.modelValue;
    },
  },

  methods: {
    handleDelete() {
      let items = this.items;
      this.$store.dispatch("content_type/remove", { items });
    },

    handlePutToBuffer() {
      let key = "contenttypes";
      let vo = {};
      vo[key] = this.items;
      this.$store.dispatch("jsonbuffer/put", { vo });
    },
  },

  props: {
    modelValue: Array,
  },
};
</script>
